import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import image from '../../assets/images/white-image.png';
import { Container } from 'semantic-ui-react';
import BlogPost from '../components/BlogPost.jsx';
import metaMarked from 'meta-marked';

function requireAll(requireContext) {
  return requireContext.keys().map(requireContext);
}

const markdowns = requireAll(require.context( `../../posts/${process.env.BUILD_LANG}`, false, /^\.\/.*\.md$/));

export default class Blog extends React.Component {
  render() {
    const parsedPosts = markdowns.map(post => metaMarked(post));
    const postComponents = [];

    parsedPosts.sort((a, b) => {
      try {
        const aDate = new Date(a.meta.date);
        const bDate = new Date(b.meta.date);

        return bDate - aDate;
      } catch(e) {
        return 0;
      } 
    });

    for (var i = 0; i < parsedPosts.length; i++) {
      const {meta: {title, date, image}, html} = parsedPosts[i];

      if (title && html) {
        const postProps = {
          title,
          date,
          image,
          html
        };

        postComponents.push(<BlogPost key={ i } { ...postProps } />);

      } else {
        console.warn('Required field missing to render blog post:', parsedPosts[i]);
      }
    }

    return <div id='blog' className='page'>
             <section>
               <Container>
                 { postComponents }
               </Container>
             </section>
           </div>
  }
}



