import React from 'react';
import { Card, Image } from 'semantic-ui-react';

export default class BlogPost extends React.Component {
  render() {
    let image;

    if (this.props.image) {
      try {
        image = require('../../posts/images/' + this.props.image);
      } catch(e) {
        console.error(e);
      }
      
    }

    return <div className='blog-post'>
             <Card>
               { image &&
                 <Image src={ image }></Image> }
               <Card.Content>
                 <Card.Header>
                   { this.props.title }
                 </Card.Header>
                 <div className='post-content' dangerouslySetInnerHTML={{__html: this.props.html}}></div>
               </Card.Content>
               <Card.Content extra>
                 {this.props.date}
               </Card.Content>
             </Card>
           </div>;
  }
}



